@use "global" as *;

.sns__wrapper {
    display: flex;
    align-items: center;
    width: 100%;
}

.sns__icon {
    display: block;
    padding-top: rem(16);
    padding-left: rem(8);
    padding-right: rem(8);
    // width: 7.2727%; //24/330
    cursor: pointer;
    
}

.sns__icon:hover {
    opacity: 0.7;
  }

.sns__icon:first-child {
    padding-left: 0;
}
.sns__icon img {
width: rem(24);
max-width: rem(32);

@include mq(md) {
width: rem(32);
}
}