@charset "UTF-8";
body, html {
  font-family: "Gotu", "sans-serif", "Noto Sans JP", "Noto Serif JP", "sans-serif", "serif", "Noto Serif JP", "serif";
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
