.button__top {
  font-size: 0.875rem;
  line-height: 1.5714;
  color: #fff;
  background-color: #408F95;
  display: block;
  text-align: center;
  text-decoration: none;
  padding: 1rem 4.875rem 1rem 2.5rem;
  transition: 0.3s ease-in-out;
  z-index: 2;
  position: relative;
}

.button__top::after {
  background-color: #fff;
  border: 0.0625rem solid #408F95;
  position: absolute;
  top: 0;
  left: 0;
  content: "";
  width: 100%;
  height: 100%;
  transform: scale(0, 1);
  transform-origin: left top;
  transition: 0.2s cubic-bezier(0.45, 0, 0.55, 1);
  z-index: -1;
}

.button__top:hover {
  color: #408F95;
}

.button__top:hover::after {
  transform: scale(1, 1);
}

.button__top span {
  position: relative;
}

.button__top span::before {
  content: "";
  position: absolute;
  top: calc(50% - 0.125rem);
  right: -2.75rem;
  transform: translateY(calc(-50% - 0.125rem)) rotate(45deg);
  width: 0.375rem;
  height: 0.0625rem;
  background: #fff;
}
@media screen and (min-width: 768px) {
  .button__top span::before {
    right: -3.0625rem;
  }
}

.button__top span::after {
  content: "";
  position: absolute;
  top: 50%;
  right: -2.75rem;
  transform: translateY(-50%);
  width: 1.875rem;
  height: 0.0625rem;
  background-color: #fff;
}
@media screen and (min-width: 768px) {
  .button__top span::after {
    width: 2.5rem;
    right: -3.125rem;
  }
}

