@use "global" as *;

.drawer__wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: $accent;
  width: 100%;
  height: 100vh;
  margin-top: rem(80);
  transition: all .7s;
  opacity: 0;
  overflow: auto;
  padding-bottom: rem(300);
  display: none;
}

.drawer__wrapper.is-active {
  display: block;
  opacity: 1;
z-index: 9999;
}

.drawer {
  display: flex;
}


.drawer__left,
.drawer__right {
    width: 50%;
}

.drawer__items {
  display: block;
  color: $white;
}

.drawer__item {
    padding-left: rem(22);
    padding-top: rem(24);
}

.drawer__item a {
  font-size: rem(16);
  display: block;
  line-height: 1.125;
}


.drawer__item--main {
    padding-top: rem(40);
    font-weight: $bold;

}

.drawer__item--main a {
  position: relative;
  
}

.drawer__item--main a::before {
    content: url(../images/common/_hitode.svg);
    display: inline-block;
  transform: scale(1.0);
  position: absolute;
  top: 0;
  left: rem(-22);
    
}

.drawer__item--main:first-of-type {
    padding-top: rem(24);
  }