@use "global" as *;

.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 9001;
  height: rem(80);
  background-color: rgba(13, 41, 54, 0.68);
  transition: all .7s;

  @include mq(md) {
    height: rem(90);
  }
}

.header.is-active {
  background-color: $accent;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: inherit;
  padding-left: rem(15);
  padding-right: rem(15);

  @include mq(md) {
    padding-left: rem(40);
    padding-right: rem(40);
  }
}

.header__inner.is-active {
}

.header__title {
  position: relative;
  z-index: 9999;
  height: inherit;
  display: flex;
  align-items: center;
  width: rem(102);

  @include mq(md) {
    width: rem(133);
  }
}

.header__title:hover {
  opacity: 0.5;
}

